.social-list {
  display: flex;
  align-items: center;
  grid-gap: clamp(12px, 1.389vw, 20px);


  &__link {
    transition: all .4s;
    color: var(--color-a);

    &:hover {
      color: var(--color-c);
    }
  }

  svg {
    fill: currentColor;
  }

  @media screen and (max-width: 1025px) {
    position: absolute;
    top: clamp(25px, 5vw, 50px);
    transform: translateY(150%);
    right: clamp(20px, 3.472vw, 50px);
  }
}
